/* Premium Casino Blackjack - $100k Design */
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700;900&family=Montserrat:wght@300;400;600;800&display=swap');

:root {
  --gold-gradient: linear-gradient(135deg, #f2d382 0%, #e6b536 25%, #d4af37 50%, #f2d382 75%, #d4af37 100%);
  --dark-gradient: linear-gradient(145deg, #1e2023 0%, #0a0c0f 100%);
  --felt-green: #0e5b43;
  --card-shadow: 0 4px 25px rgba(0, 0, 0, 0.5);
  --btn-shadow: 0 6px 15px rgba(0, 0, 0, 0.4);
  --premium-shadow: 0 8px 32px rgba(0, 0, 0, 0.35);
  --glow-effect: 0 0 15px rgba(212, 175, 55, 0.6);
  --gold-subtle: #a89462;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

body {
  background: #030303 url('https://c1.wallpaperflare.com/path/6/487/674/various-business-gambling-money-b53308a7b043deae6cfb47c5b417f6fb.jpg') center/cover fixed;
  color: white;
  min-height: 100vh;
  overflow-x: hidden;
  position: relative;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(5px);
  z-index: -1;
}

.blackjack-container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 40px 20px;
  position: relative;
}

/* Decorative elements */
.blackjack-container::before,
.blackjack-container::after {
  content: "";
  position: absolute;
  width: 150px;
  height: 150px;
  border: 4px solid rgba(212, 175, 55, 0.3);
  z-index: -1;
}

.blackjack-container::before {
  top: 20px;
  left: 5px;
  border-right: none;
  border-bottom: none;
}

.blackjack-container::after {
  bottom: 20px;
  right: 5px;
  border-left: none;
  border-top: none;
}

/* Game title with luxury styling */
.game-title {
  font-family: 'Playfair Display', serif;
  font-weight: 900;
  font-size: 4.5rem;
  text-align: center;
  margin-bottom: 30px;
  background: var(--gold-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  text-shadow: 0 2px 15px rgba(212, 175, 55, 0.3);
  letter-spacing: 2px;
  position: relative;
  padding-bottom: 20px;
}

.game-title::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
  height: 3px;
  background: var(--gold-gradient);
  box-shadow: var(--glow-effect);
}

/* Game area styling */
.game-area {
  background: var(--dark-gradient);
  border-radius: 20px;
  padding: 40px;
  box-shadow: var(--premium-shadow);
  position: relative;
  overflow: hidden;
  border: 1px solid rgba(212, 175, 55, 0.3);
}

.game-area::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: var(--gold-gradient);
}

/* Button container */
.button-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 25px 0;
}

.left-buttons {
  display: flex;
  gap: 15px;
}

/* Button styling */
.game-button, .new-game-button {
  background: var(--dark-gradient);
  color: white;
  border: 2px solid #d4af37;
  border-radius: 50px;
  font-size: 1.2rem;
  padding: 12px 35px;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
  min-width: 140px;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: var(--btn-shadow);
}

.game-button:hover, .new-game-button:hover {
  background: var(--gold-gradient);
  color: #000;
  transform: translateY(-3px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
}

.game-button:active, .new-game-button:active {
  transform: translateY(1px);
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.5);
}

.game-button:disabled, .new-game-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  border-color: #666;
  background: #333;
  color: #999;
  transform: none;
  box-shadow: none;
}

.new-game-button {
  background: var(--gold-gradient);
  color: #000;
  font-weight: 800;
  padding: 14px 40px;
  box-shadow: 0 8px 20px rgba(212, 175, 55, 0.4);
}

.deal-button {
  background: linear-gradient(145deg, #135e8e 0%, #0a3963 100%);
  border-color: #2f80b9;
}

.deal-button:hover {
  background: linear-gradient(145deg, #1d8bcf 0%, #155a94 100%);
}

/* Balance and bet container */
.balance-bet-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 40px 0;
  padding: 25px;
  background: rgba(14, 91, 67, 0.7);
  border-radius: 12px;
  border: 1px solid rgba(212, 175, 55, 0.5);
  box-shadow: var(--card-shadow);
}

.balance-info {
  background: rgba(0, 0, 0, 0.5);
  padding: 15px 30px;
  border-radius: 8px;
  border: 1px solid rgba(212, 175, 55, 0.3);
}

.balance-info h3 {
  font-size: 1.8rem;
  font-weight: 600;
  color: #f2d382;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  font-family: 'Montserrat', sans-serif;
}

.bet-input-container {
  display: flex;
  flex-direction: column;
}

.bet-label {
  font-size: 1.4rem;
  margin-bottom: 12px;
  font-weight: 600;
  color: white;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
}

.bet-input {
  background: rgba(0, 0, 0, 0.7);
  border: 2px solid #d4af37;
  color: #f2d382;
  font-size: 1.5rem;
  padding: 12px 20px;
  border-radius: 8px;
  width: 250px;
  box-shadow: var(--card-shadow);
  transition: all 0.3s ease;
}

.bet-input:focus {
  outline: none;
  border-color: #f2d382;
  box-shadow: 0 0 15px rgba(242, 211, 130, 0.5);
}

/* Hands container */
.hands-container {
  display: flex;
  justify-content: space-between;
  gap: 40px;
  margin-top: 40px;
}

.hand {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px;
  border-radius: 15px;
  background: rgba(14, 91, 67, 0.8);
  box-shadow: var(--card-shadow);
  position: relative;
  overflow: hidden;
  transition: all 0.3s ease;
  border: 1px solid rgba(212, 175, 55, 0.3);
}

.hand::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('https://www.transparenttextures.com/patterns/pool-table.png');
  opacity: 0.05;
  z-index: -1;
}

.hand:hover {
  transform: translateY(-5px);
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.6);
}

.hand h2 {
  font-family: 'Playfair Display', serif;
  font-size: 2.2rem;
  margin-bottom: 25px;
  color: #f2d382;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  position: relative;
  width: 100%;
  text-align: center;
}

.hand h2::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background: var(--gold-gradient);
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 20px 0;
  min-height: 200px;
}

.card-image {
  width: 120px;
  height: auto;
  border-radius: 10px;
  box-shadow: var(--card-shadow);
  transform: rotate(0deg);
  transition: all 0.3s ease;
  margin: 5px;
}

.card-image:hover {
  transform: translateY(-15px) rotate(0deg);
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.8);
  z-index: 10;
}

.player-hand .card-image:nth-child(odd) {
  transform: rotate(-2deg);
}

.player-hand .card-image:nth-child(even) {
  transform: rotate(2deg);
}

.dealer-hand .card-image:nth-child(odd) {
  transform: rotate(1deg);
}

.dealer-hand .card-image:nth-child(even) {
  transform: rotate(-1deg);
}

.hand h3 {
  margin-top: 20px;
  font-size: 1.8rem;
  color: white;
  background: rgba(0, 0, 0, 0.5);
  padding: 10px 30px;
  border-radius: 50px;
  border: 1px solid rgba(212, 175, 55, 0.5);
}

/* Result popup styling */
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  backdrop-filter: blur(8px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.popup-inner {
  position: relative;
  padding: 50px;
  width: 90%;
  max-width: 600px;
  background: var(--dark-gradient);
  border-radius: 15px;
  border: 2px solid var(--gold-subtle);
  box-shadow: 0 0 50px rgba(168, 148, 98, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  animation: popupFadeIn 0.5s ease forwards;
}

@keyframes popupFadeIn {
  from {
    opacity: 0;
    transform: translateY(-50px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.popup-inner::before {
  content: "";
  position: absolute;
  top: -3px;
  left: -3px;
  right: -3px;
  bottom: -3px;
  background: var(--gold-gradient);
  z-index: -1;
  border-radius: 17px;
  opacity: 0.4;
}

.popup-inner h3 {
  font-family: 'Playfair Display', serif;
  font-size: 2.8rem;
  color: white;
  text-align: center;
  margin-bottom: 30px;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  background: var(--gold-gradient);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.close-button {
  position: absolute;
  top: 0px;
  right: 0px;
  padding: 10px;
  background: transparent;
  border: none;
  text-shadow: 0 2px 10px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  font-size: 1.5rem;
  color: #d4af37;
  transition: all 0.3s ease;
}

.close-button:hover {
  color: white;
  transform: scale(1.2);
}

/* Responsive adjustments */
@media (max-width: 1200px) {
  .hands-container {
    flex-direction: column;
  }
  
  .hand {
    width: 100%;
  }
  
  .game-title {
    font-size: 3.5rem;
  }
}

@media (max-width: 768px) {
  .button-container, .balance-bet-container {
    flex-direction: column;
    gap: 20px;
  }
  
  .left-buttons {
    width: 100%;
    justify-content: space-between;
  }
  
  .game-button, .new-game-button {
    width: 100%;
    margin: 5px 0;
  }
  
  .bet-input {
    width: 100%;
  }
  
  .game-title {
    font-size: 2.8rem;
  }
  
  .card-image {
    width: 100px;
  }
}

/* Card dealing animation */
@keyframes dealCard {
  0% {
    opacity: 0;
    transform: translateY(-100px) rotate(20deg);
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 1;
    transform: translateY(0) rotate(0deg);
  }
}

.card-image {
  animation: dealCard 0.5s ease-out forwards;
}

/* Chip pile decoration */
.balance-info::before {
  content: "";
  position: absolute;
  width: 40px;
  height: 40px;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="50" cy="50" r="40" fill="%23d4af37" stroke="%23000" stroke-width="2"/><circle cx="50" cy="50" r="30" fill="%23e6b536" stroke="%23000" stroke-width="1"/><text x="50" y="55" font-family="Arial" font-size="15" font-weight="bold" text-anchor="middle" fill="%23000">$</text></svg>');
  background-size: contain;
  left: -20px;
  top: 50%;
  transform: translateY(-50%);
  filter: drop-shadow(0 2px 5px rgba(0, 0, 0, 0.5));
}

/* Ambient lighting effect */
.game-area::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: radial-gradient(circle at 50% 0%, rgba(212, 175, 55, 0.1) 0%, transparent 50%);
  pointer-events: none;
}

/* Card flip animation for dealer's hidden card */
@keyframes flipCard {
  0% {
    transform: rotateY(180deg);
    opacity: 0.8;
  }
  100% {
    transform: rotateY(0deg);
    opacity: 1;
  }
}

.dealer-hand .card-image:first-child {
  animation: flipCard 0.8s ease-out forwards;
}

/* Table felt texture */
.hands-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url('https://www.transparenttextures.com/patterns/green-felt.png');
  opacity: 0.05;
  pointer-events: none;
}

/* Chip stack animation for bet */
@keyframes chipStack {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}

.bet-input-container::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 30px;
  background-image: url('data:image/svg+xml;utf8,<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100"><circle cx="50" cy="50" r="48" fill="%23e6b536" stroke="%23000" stroke-width="2"/><circle cx="50" cy="50" r="35" fill="%23d4af37" stroke="%23000" stroke-width="1"/><text x="50" y="58" font-family="Arial" font-size="20" font-weight="bold" text-anchor="middle" fill="%23000">$</text></svg>');
  background-size: contain;
  right: 10px;
  bottom: 10px;
  animation: chipStack 2s ease-in-out infinite;
}